.pincodeDiv {
  width: 28rem;
  margin-left: 18%;
  margin-top: 5.5rem;
}

.pInput {
  margin-top: 25px;
  width: 240px;
  height: 2.8rem;
  padding: 8px 96px;
  border: 1.2px solid rgb(0, 0, 0, 0.2);
  box-sizing: border-box;
  border-radius: 4px;
}

.pInput:focus {
  outline: none !important;
  border-color: #9364ed;
  box-shadow: 0 0 10px #9364ed;
}

.btn1 {
  padding: 8px 32px;
  width: 10rem;
  height: 3rem;
  background: #512dd1;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  color: white;
  margin-top: 15px;
  margin-left: 30px;
}

.pindivflex {
  max-width: 280px;
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.innerPincodeDiv {
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
}
.pDiv {
  font-size: 10px;
  line-height: 20px;

  color: black;
}
.pInnerDiv {
  font-size: 0.7rem;
  align-items: center;
}

@media only screen and (max-width: 1100px) {
  .pincodeDiv {
    min-width: 300px;
  }
}
